}

.friendImageAndText {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    padding: 20px;
}

.friendImageAndText img {
    width: 40px;
    height: 40px;
    margin: 5px;
}

.friendText {
    height: 60px;
    color: #000;
    background-color: #fff;
    border: 1px solid rgba(141, 130, 130, 0.49);
    border-radius: 10px;
    padding: 5px 15px
}

.friendText:after {

}

.friendName {
    font-size: 17px;
    text-align: left;
}

.friendMessageText {
    font-size: 15px;
    text-align: center;
    margin: 5px 0;
}

.friendTime {
    font-size: 12px;
    color: #000;
    margin-top: 5px;
}
