.button {
    width: 130px;
    height: 30px;
    border: none;
    border-radius: 3px;
    font-weight: 600;
    font-family: Montserrat;
    color : #FFFFFF;
    transition: transform 0.1s, width 0.1s;
}

.disabled {
    background-color: #004D99;
    color: #002E5C;
    opacity: 50%;
}

.secondary {
    background-color: #FFFFFF;
    color: #0066CC;
    border: 1px solid #0066CC;
}

.default {
    background-color: #0066CC;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    cursor: pointer;
    background-color: #0080FF;
    transform: scaleX(1.20);
}

.red:hover {
    cursor: pointer;
    background-color: #FF1A47;
    transform: scaleX(1.20);
}

.secondary:hover {
    cursor: pointer;
    background-color: #E5F0FA;
    transform: scaleX(1.15);
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
}
